import React from "react";
import {
  BrowserRouter as Router,
  Switch,
  Redirect,
  Route
} from "react-router-dom";
import { AuthRouter } from "./AuthRouter";

export const AppRouter = () => {



  return (
    <Router>
        <div>
        <Switch>
                <Route path="/" component={AuthRouter}/>
                <Redirect to="/index" />
        </Switch>
        </div>
    </Router>
  );
}